import React from 'react';
import SvgIcons from './svgIcons';
import { Link } from 'react-router'

const iconColor = '#00838F';

class NavContainer extends React.Component {
    render() {
        const { HomeIcon, PlanIcon, AppIcon, UserIcon, ListIcon, HelpIcon } = SvgIcons;
        return (
            <ul className="nav-container">
                <li>
                    <Link to={'/home'}>
                        <HomeIcon color={iconColor}/>
                        <p>主页</p>
                    </Link>
                </li>
                <li>
                    <Link to={'/info'}>
                        <PlanIcon color={iconColor}/>
                        <p>介绍</p>
                    </Link>
                </li>
                <li>
                    <Link to={'/achieve'}>
                        <AppIcon color={iconColor}/>
                        <p>成果</p>
                    </Link>
                </li>
                <li>
                    <Link to={'/teacher'}>
                        <UserIcon color={iconColor}/>
                        <p>教师</p>
                    </Link>
                </li>
                <li>
                    <Link to={'/awards'}>
                        <ListIcon color={iconColor}/>
                        <p>奖项</p>
                    </Link>
                </li>
                <li>
                    <Link to={`/help`}>
                        <HelpIcon color={iconColor}/>
                        <p>帮助</p>
                    </Link>
                </li>
            </ul>
        );
    }
}

export default NavContainer;




